// Environment-specific API configuration
const getApiBase = () => {
  // For GitHub Pages deployment
  if (window.location.hostname === 'derek923563.github.io') {
    // You need to deploy your backend to a hosting service like Heroku, Railway, or DigitalOcean
    // and replace this URL with your actual backend URL
    return process.env.VITE_PROD_API_URL || 'https://your-backend-url.herokuapp.com/api';
  }
  // For local development
  return import.meta.env.VITE_API_BASE || 'http://localhost:5000/api';
};

const API_BASE = getApiBase();

function getToken() {
  return localStorage.getItem('token');
}

async function request(method: string, url: string, data?: any) {
  const headers: Record<string, string> = {
    'Content-Type': 'application/json',
  };
  const token = getToken();
  if (token) headers['Authorization'] = `Bearer ${token}`;

  try {
    const res = await fetch(`${API_BASE}${url}`, {
      method,
      headers,
      body: data ? JSON.stringify(data) : undefined,
      credentials: 'include', // Important for CORS with authentication
    });

    if (!res.ok) {
      const error = await res.json().catch(() => ({}));
      throw new Error(error.message || 'API error');
    }

    return res.json();
  } catch (error) {
    console.error(`API ${method} request failed:`, error);
    throw error;
  }
}

export const api = {
  get: (url: string) => request('GET', url),
  post: (url: string, data?: any) => request('POST', url, data),
  put: (url: string, data?: any) => request('PUT', url, data),
  delete: (url: string) => request('DELETE', url),
}; 